export default {
	data() {
		return {
			scrollHeight: '',
			leftArray: [],
			mainArray: [],
			topArr: [],
			leftIndex: 0,
			isMainScroll: false,
			scrollInto: ''
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res) => {
				/* 设置当前滚动容器的高，若非窗口的告诉，请自行修改 */
				this.scrollHeight = `${res.windowHeight}px`;
			}
		});
	},
	computed: {

	},
	mounted() {
		this.getListData();
	},
	methods: {
		/* 获取列表数据 */
		getListData() {
			this.$api.getCategoryList(res => {
				if (res.code) {
					this.leftArray = res.leftArray;
					this.mainArray = res.mainArray;
					this.$nextTick(() => {
						this.getElementTop();
					});
				}
			})

		},
		/* 获取元素顶部信息 */
		getElementTop() {
			/* Promise 对象数组 */
			let p_arr = [];

			/* 新建 Promise 方法 */
			let new_p = (selector) => {
				return new Promise((resolve, reject) => {
					let view = uni.createSelectorQuery().select(selector);
					view.boundingClientRect(data => {
						resolve(data.top)
					}).exec();
				})
			}

			/* 遍历数据，创建相应的 Promise 数组数据 */
			this.mainArray.forEach((item, index) => {
				p_arr.push(new_p(`#item-${index}`));
			});

			/* 主区域滚动容器的顶部距离 */
			new_p("#scroll-el").then((res) => {
				let top = res;
				/* 所有节点信息返回后调用该方法 */
				Promise.all(p_arr).then((data) => {
					this.topArr = data;
				});
			})
		},
		/* 主区域滚动监听 */
		mainScroll(e) {
			if (!this.isMainScroll) {
				return;
			}
			let top = e.detail.scrollTop;
			let index = -1;

			if (top >= this.topArr[this.topArr.length - 1]) {
				index = this.topArr.length - 1;
			} else {
				index = this.topArr.findIndex((item, index) => {
					return this.topArr[index + 1] >= top;
				});
			}
			this.leftIndex = (index < 0 ? 0 : index);
		},
		/* 主区域触摸 */
		mainTouch() {
			this.isMainScroll = true;
		},
		/* 左侧导航点击 */
		leftTap(e) {
			let index = e.currentTarget.dataset.index;
			this.isMainScroll = false;
			this.leftIndex = Number(index);
			this.scrollInto = `item-${index}`;

		},
		toProductList(e) {
			let key = e.currentTarget.dataset.key || "";
			let onekey = e.currentTarget.dataset.onekey || "";
			if (key == '所有商品') {
				uni.navigateTo({
					url: '../productList/productList'
				})
			} else {
				uni.navigateTo({
					url: '../productList/productList?searchKey=' + onekey + "-" + key
				})
			}
		}
	}
}
